.textLabel {
  top: 62px;
  left: 18px;
  line-height: 20px;
  opacity: 0.7;
  mix-blend-mode: normal;
}
.form,
.input,
.textLabel {
  position: absolute;
}
.input {
  top: 36px;
  left: 18px;
  font-size: var(--font-size-base);
  line-height: 16px;
  color: var(--color-darkslategray-100);
}
.form {
  top: 14px;
  left: 32px;
  line-height: 12px;
  text-transform: uppercase;
  opacity: 0.3;
  mix-blend-mode: normal;
}
.iconarrowdown,
.iconarrowtop,
.iconcalendarfill {
  position: absolute;
  width: 12px;
  height: 12px;
  overflow: hidden;
}
.iconcalendarfill {
  top: 14px;
  left: 18px;
}
.iconarrowdown,
.iconarrowtop {
  right: 18px;
}
.iconarrowdown {
  bottom: 12px;
  display: none;
}
.iconarrowtop {
  top: 12px;
}
.rectangle,
.rectangleCopy6,
.rectangleCopy7 {
  position: absolute;
  height: 100%;
  width: 24.32%;
  top: 0;
  right: 75.68%;
  bottom: 0;
  left: 0;
  border-radius: var(--br-mid-5);
  background-color: var(--color-lightgray);
}
.rectangleCopy6,
.rectangleCopy7 {
  right: 37.84%;
  left: 37.84%;
}
.rectangleCopy7 {
  right: 0;
  left: 75.68%;
}
.rectangleParent {
  position: absolute;
  height: 23.87%;
  width: 98.15%;
  top: 38.06%;
  right: 0;
  bottom: 38.06%;
  left: 1.85%;
}
.iconoptions3dots {
  position: absolute;
  bottom: 14px;
  left: 18px;
  width: 16px;
  height: 16px;
  overflow: hidden;
  display: none;
}
.forminputprimary {
  position: absolute;
  height: 27.41%;
  width: 24.8%;
  top: 60.75%;
  right: 71.4%;
  bottom: 11.84%;
  left: 3.8%;
  border-radius: var(--br-5xs);
  background-color: var(--color-solid-medium-2-5);
  border: 1px solid var(--color-whitesmoke-200);
  box-sizing: border-box;
  overflow: hidden;
  text-align: left;
  font-size: var(--font-size-xs);
  color: var(--color-slategray-300);
  font-family: var(--font-open-sans);
}
