@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700;800&display=swap");
@import url("https://fonts.googleapis.com/icon?family=Material+Icons");

body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-open-sans: "Open Sans";

  /* font sizes */
  --font-size-mini: 15px;
  --font-size-xs: 12px;
  --font-size-base: 16px;
  --font-size-sm: 14px;
  --font-size-5xl: 24px;
  --font-size-lg: 18px;

  /* Colors */
  --grey-1: #fff;
  --color-solid-primary-100: #0a6ada;
  --color-gray-100: #0b1023;
  --color-gray-200: rgba(0, 0, 0, 0);
  --color-lavender: #dae1fb;
  --color-solid-medium-2-5: rgba(124, 130, 155, 0.05);
  --color-whitesmoke-100: #f5f6f6;
  --color-whitesmoke-200: #eef0f4;
  --color-lightgray: #cccfd9;
  --color-slategray-100: #6c738f;
  --color-slategray-200: #596181;
  --color-slategray-300: #475073;
  --color-darkslategray-100: #2e3758;
  --color-darkslategray-200: #282e44;
  --color-solid-primary-2-100: #163668;
  --color-aliceblue: #e2e8f2;
  --color-steelblue-100: rgba(108, 134, 177, 0.2);
  --color-crimson-100: #df142d;
  --color-solid-secondary-06: rgba(223, 20, 45, 0.06);
  --color-lightsteelblue: #a2a8bf;
  --color-solid-others-100: #ffc54d;

  /* Gaps */
  --gap-20xl: 39px;

  /* border radiuses */
  --br-161xl: 180px;
  --br-xs: 12px;
  --br-13xl: 32px;
  --br-5xs: 8px;
  --br-mid-5: 17.5px;
  --br-sm: 14px;
  --br-7xs: 6px;
}
